<template lang="html">
    <div class="index" id="home">
        <div id="main-content">
            <div id="content" v-show="firstLevel==0">
                <div id="content-title">
                    <h3 id="from-here">Simulation System</h3>
                    <p id="welcome">Welcome, please choose one of the options</p>
                </div>
                <div id="options">
                    <div id="blank-button"></div>
                    <button id="first-button" type="button" name="button" class="option-button" @click="getFirstPagesByIndex(1)">Start A New Simulation</button>
                    <button type="button" name="button" class="option-button" >Import An Existed Simulation</button>
                </div>
            </div>
            <transition name="bounce">
                <div class="start-container" v-show="firstLevel==1">
                    <div id="start-container-tab">
                        <a class="start-tab" v-bind:class="{ 'start-tab-active': !noJobs }">
                            Job
                        </a>
                        <a class="start-tab" v-bind:class="{ 'start-tab-active': !noArrivalRate }">
                            Arrival Rate
                        </a>
                        <a class="start-tab" v-bind:class="{ 'start-tab-active': !noServerCluster }">
                            Server
                        </a>
                        <a class="start-tab" v-bind:class="{ 'start-tab-active': !noAlgorithm }">
                            Scheduling Algorithm
                        </a>
                    </div>

                    <div id="start-table-container" v-show="newSimulationPage==0">
                        <div id="start-table-add">
                            <a id="start-add" @click="popUpConfigure()">Add</a>
                        </div>
                        <div id="start-table-title">
                            <table class="table-content">
                                <tr>
                                    <td class="table-td">Job Class</td>
                                    <td class="table-td">Job Size Distribution</td>
                                    <td class="table-td">Speedup Function</td>
                                    <td class="table-td">Operation</td>
                                </tr>
                            </table>

                        </div>
                        <div id="start-table-content">
                            <transition-group name="list" tag="p">
                                <table class="table-content" v-for="(job, index) in currJobs" v-bind:key="index">
                                    <tr>
                                        <td class="table-td">Class_{{index + 1}}</td>
                                        <td class="table-td">{{job.sizeFun}}<br>Parameters: {{job.sizeFunPara}}</td>
                                        <td class="table-td">{{job.speedupFun}}<br>Parameters: {{job.speedupFunPara}}</td>
                                        <td class="table-td" @click="deleteAJobByIndex(index)"><a>Delete</a></td>
                                    </tr>
                                </table>
                            </transition-group>
                        </div>

                        <div class="last-next">
                            <button type="button" name="button" class="last-next-button" style="float:left" @click="getFirstPagesByIndex(0)">&larr; Last</button>
                            <button type="button" name="button" class="last-next-button" :disabled='noJobs' @click="getPagesByIndex(1)" v-bind:class="{ 'last-next-button-noactive': noJobs }" style="float:right">Next &rarr;</button>
                        </div>

                    </div>

                    <div id="start-rate-container" v-show="newSimulationPage==1">
                        <div class="rate-server-input">
                            <span>Arrival Rate <br><span style="font-size:12px;">Arrival rate 𝜆≥0 of new jobs</span><br><br></span>
                            <input type="text" class="rate-server" placeholder="Arrival rate 𝜆≥0 of new jobs" v-model="arrivalRate">
                        </div>
                        <div class="last-next">
                            <button type="button" name="button" class="last-next-button" style="float:left" @click="getPagesByIndex(0)">&larr; Last</button>
                            <button type="button" name="button" class="last-next-button" :disabled='noArrivalRate' @click="getPagesByIndex(2)" v-bind:class="{ 'last-next-button-noactive': noArrivalRate }" style="float:right">Next &rarr;</button>
                        </div>
                    </div>

                    <div id="start-server-container" v-show="newSimulationPage==2">
                        <div class="rate-server-input">
                            <span>Server Cluster <br><span style="font-size:12px;">Only a single parameter: number of servers 𝑁 > 0</span><br><br></span>
                            <input type="text" class="rate-server" placeholder="Only a single parameter: number of servers 𝑁 > 0" v-model="serverCluster">
                        </div>
                        <div class="last-next">
                            <button type="button" name="button" class="last-next-button" style="float:left" @click="getPagesByIndex(1)">&larr; Last</button>
                            <button type="button" name="button" class="last-next-button" :disabled='noServerCluster' @click="getPagesByIndex(3)" v-bind:class="{ 'last-next-button-noactive': noServerCluster }" style="float:right">Next &rarr;</button>
                        </div>
                    </div>

                    <div id="start-algorithm-container" v-show="newSimulationPage==3">
                        <div class="rate-algorithm-select">
