                            <button type="button" name="button" class="last-next-button" :disabled='noAlgorithm' @click="getPagesByIndex(4)" v-bind:class="{ 'last-next-button-noactive': noAlgorithm }" style="float:right;background-color:#36bea6;">Simulate</button>
                        </div>
