</template>

<script>
import Swal from 'sweetalert2'

export default {
    name: 'index',
    data: function() {
        return {
            //page
            firstLevel: 0,
            newSimulationPage: 0,
            // other
            currJobs: this.GLOBAL.schedCore.config.jobs,
            arrivalRate: this.GLOBAL.schedCore.config.arrivalRate,
            serverCluster: this.GLOBAL.schedCore.config.serverCluster,
            algorithmName: this.GLOBAL.schedCore.config.algorithmName,
            algorithm: this.GLOBAL.schedCore.config.algorithm,
        }
    },
    components: {

    },
    mounted: function() {
        // window.onbeforeunload = function(event){
        //     return true
        // }
    },
    computed: {
        noJobs: function() {
            return (this.currJobs.length == null || this.currJobs.length == 0)
        },
        noArrivalRate: function() {
            return (this.arrivalRate == null || this.arrivalRate == '' || this.arrivalRate < 0 || isNaN(this.arrivalRate))
        },
        noServerCluster: function() {
            return (this.serverCluster == null || this.serverCluster == '' || this.serverCluster < 0 || isNaN(this.serverCluster))
        },
        noAlgorithm: function() {
            return (this.algorithmName == null || this.algorithmName == '' || this.algorithm == null || this.algorithm == '')
        }
    },
    watch: {
        arrivalRate: function (oldval, newval) {
            if(this.arrivalRate == '' || isNaN(this.arrivalRate)) {
                if(isNaN(oldval)) {
                    this.GLOBAL.schedCore.config.arrivalRate = 1
                }else {
                    this.GLOBAL.schedCore.config.arrivalRate = oldval
                }
            }else {
                this.GLOBAL.schedCore.config.arrivalRate = this.arrivalRate
            }
        },
        serverCluster: function (oldval, newval) {
            if(this.serverCluster == '' || isNaN(this.serverCluster)) {
                if(isNaN(oldval)) {
                    this.GLOBAL.schedCore.config.serverCluster = 16
                }else {
                    this.GLOBAL.schedCore.config.serverCluster = oldval
                }
            }else {
                this.GLOBAL.schedCore.config.serverCluster = this.serverCluster
            }
        },
        algorithmName: function (oldval, newval) {
            this.GLOBAL.schedCore.config.algorithmName = this.algorithmName
        },
        algorithm: function (oldval, newval) {
            this.GLOBAL.schedCore.config.algorithm = this.algorithm
        }
    },
    methods: {
        popUpConfigure: function() {
            var that = this
            const { value: formValues } =  Swal.fire({
                  title: 'Add a new job class',
                  html: '<div style="width:100%;text-align:center;">Select A Job Size Distribution</div>' +
                        '<select class="swal2-select" id="swal-select1" style="display: flex;"> \
                            <option value="deterministic">Deterministic D𝑒𝑡(𝑐)</option> \
                            <option value="uniform">Uniform U𝑛𝑖𝑓(𝑎,𝑏)</option> \
                            <option value="exponential">Exponential E𝑥𝑝(𝜆)</option> \
                            <option value="erlang">Erlang E𝑟𝑙𝑎𝑛𝑔(𝑛,𝜆)</option> \
                            <option value="pareto">Pareto P𝑎𝑟𝑒𝑡𝑜(𝛼,𝜎,𝜇)</option> \
                        </select>' +
                        '<input id="swal-input1" class="swal2-input" value="" placeholder="please input parameters (separated by comma)">' +
                        '<hr/>' +
                        '<div style="width:100%;text-align:center;">Select A Speedup Function</div>' +
                        '<select class="swal2-select" id="swal-select2" style="display: flex;"> \
                            <option value="power">Power function</option> \
                            <option value="amdahl’s">Amdahl’s law</option> \
                            <option value="manual">Manual</option> \
                        </select>' +
                        '<input id="swal-input2" class="swal2-input" value="" placeholder="please input parameters (separated by comma)">' ,
                  focusConfirm: false,
                  preConfirm: () => {
                    return [
                      document.getElementById('swal-select1').value,
                      document.getElementById('swal-input1').value,
                      document.getElementById('swal-select2').value,
                      document.getElementById('swal-input2').value
                    ]
                  }
                }).then((result) => {
                  if (result.value) {
                    var job = {
                        'sizeFun': result.value[0],
                        'sizeFunPara': result.value[1],
                        'speedupFun': result.value[2],
                        'speedupFunPara': result.value[3]
                    }
                    that.GLOBAL.schedCore.config.jobs.unshift(job)
                    // const answers = JSON.stringify(result.value)
                    // Swal.fire({
                    //   title: 'All done!',
                    //   html: `
                    //     Your answers:
                    //     <pre><code>${answers}</code></pre>
                    //   `,
                    //   confirmButtonText: 'Done!'
                    // })
                  }
                })
        },

        deleteAJobByIndex: function(index) {
            this.GLOBAL.schedCore.config.jobs.splice(index, 1)
        },
        getFirstPagesByIndex: function (index) {
            if(index < 4) {
                this.firstLevel = index
            }
        },

        getPagesByIndex: function (index) {
            if(index < 4) {
                this.newSimulationPage = index
            }else {
                this.$router.push({ name: "Simulation"})
            }
        },



    }
}
</script>

